@use '../../foundation/variable' as var;

.js-trigger.is-show{ 
  opacity: 0;
  transition: opacity 0.3s ease;
}

.js-trigger.is-show--done{ 
  opacity: 1;
}

.p-main-visual {
}

.swiper-button-next, 
.swiper-button-prev {
  top: auto!important;
  bottom: -0.7576%!important;
  right: auto!important;
  width: 4.2683%!important;;
}
.swiper-button-prev{
  left: 0!important;
}
.swiper-button-next{
  left: 9.9593%!important;
}
.swiper-button-next:after, 
.swiper-button-prev:after {
  width: 100%;
  height: 100%;
  font-size: 0!important;
}
.swiper-button-prev:after {
  background: url(../../../img/common/icon-arrow.svg) center center / contain no-repeat;
  transform: rotate(180deg);
}
.swiper-button-next:after {
  background: url(../../../img/common/icon-arrow.svg) center center / contain no-repeat;
}
